
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
html {
  background-color: #454c62;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
.disable {
  display: none !important;
}
.select {
  overflow: hidden;
  overflow-y: scroll;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: sans-serif;
  height: 100%;
  width: 14em;
  -webkit-transition: width .2s;
  transition: width .2s;
}
.select a {
    text-decoration: none;
    font-weight: 400;
    color: white;
    display: block;
    text-align: left;
    width: 100%;
    padding: 1.2em;
}
.select a:visited {
    color: white;
}
.select a:hover {
    color: #32C994;
    background-color: #36719d;
}
.hide {
  width: 0;
}
.hide #menuToggle {
    position: absolute;
    right: 1em;
    display: inline;
    width: auto;
}
body {
  padding: 0;
  margin: 0;
}
#menuToggle {
  width: 2em;
  padding: 0;
  position: relative;
  top: .5em;
  right: 2.5em;
  cursor: pointer;
  margin-bottom: 2em;
}
#menuToggle:hover {
  background-color: unset;
}
#anim {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



